import "../styles/Button.css";

/**
 * @param {text} Texto que contiene el botn.
 * @param {handleClick} Funcin que maneja los clicks en el botn.
 * @param {disabled} Booleano que nos dice si el botn est deshabilitado.
 * @param {focused} Booleano que nos dice si el bton est presionado.
 * @returns Componente que renderiza los botones de la paginacin.
 */
const Button = ({ text, handleClick, disabled, focused }) => {
  return (
    <button
      className={
        disabled
          ? "button button--disabled"
          : "button" && focused
          ? "button button--focused"
          : "button"
      }
      onClick={() => handleClick()}
      disabled={disabled}
    >
      {text}
    </button>
  );
};

export default Button;
